body{
  font-family: 'Roboto Condensed', sans-serif;
}
:root{
  --primary-white: #fff;
  --primary-orange: #f9ab00;
  --primary-dark: rgba(255,255,255, 0);
  --primary-red: #ef403e;
  --priamry-hover-red: #d40805;
  /* --priamry-hover-red: #f33501; */
}
/***************** Navbar *******8********/

nav{
 z-index: 2;
 /* position: fixed !important; */
}

.logo
{
  /* width: 16.25rem; */
  text-transform: uppercase;
}

/*------Manu Link------- */
nav a.nav-link{
  font-weight: 500;
  letter-spacing: 0.0375rem;
  cursor: pointer;
}

.navbar-nav > li > a{
  margin-right: 0.75rem;
  margin-left: 0.75rem;
  color: white !important;
  border-top: 0.1875rem solid rgba(255,255,255, 0);
  border-top: 0.1875rem solid var(--primary-dark);
}
.navbar-brand{
  max-width: 17.25rem;
  padding: 0 !important;
  /* height: 6rem !important; */
}

nav ul li{
  text-transform: uppercase;
}

.navbar-nav > li > a.active, .navbar-nav > li >a.active:focus{
  border-top: 0.1875rem solid #f9ab00;
  border-top: 0.1875rem solid var(--primary-orange);
}
.navbar-nav > li > a:hover{
  border-top: 0.1875rem solid #f9ab00;
  border-top: 0.1875rem solid var(--primary-orange);
}


/********************* Header ******************/

.header-wraper{
  position: relative;
  background: url(/portfolio-project/static/media/dark-desktop.1d4ff097.jpg) no-repeat;
  background-size: cover;
  background-position: center;
  height: 90.7vh;
}
.main-info{
 display: -webkit-flex;
 display: flex;
 position: absolute;
 top: 0;
 left: 0;
 right: 0;
 bottom: 0;
 -webkit-flex-direction: column;
         flex-direction: column;
 -webkit-justify-content: center;
         justify-content: center;
 -webkit-align-items: center;
         align-items: center;
 text-align: center;
 font-family: 'Roboto Condensed', sans-serif;
 z-index: 1;
}
.main-info h1{
  color: #f9ab00;
  color: var(--primary-orange);
  text-transform: uppercase;
}
@media(max-width: 768px){
  .main-info h1{
    font-size: 1.7rem !important;
  }
}

/* ****************** Typed Text ************************ */

.typed-text{
  font-size: 3rem;
  color: #fff;
  color: var(--primary-white);
}
@media(max-width: 768px){
  .typed-text{
    font-size: 1.7rem !important;
  }
  .btn-main-offer{
    font-size: 0.9rem !important;
  }
}

/* ****************** Header main offer Button ************************ */

.btn-main-offer{
  border: 1px solid #ef403e;
  border: 1px solid var(--primary-red);
  text-transform: uppercase;
  border-radius: 0.6rem;
  padding: 0.75rem 0.75rem 0.75rem 0.75rem;
  color: #ef403e;
  color: var(--primary-red);
  margin: 2rem;
}

.btn-main-offer:hover{
  text-decoration: none;
  color: #fff;
  color: var(--primary-white);
  background-color: #d40805;
  background-color: var(--priamry-hover-red);
  transition: .2s ease-in-out;
}

