  .cube,
  .cube * {
    position: absolute;
    width: 6rem;
    height: 6rem;
  }
  .cube .sides * {
    box-sizing: border-box;
    border: 0.12rem solid white;
    border-radius: 0.25rem;
    /* background: rgba(46, 46, 46, 0.1); */
    background: rgba(255, 255, 255, 0.1);
  }
  
  @-webkit-keyframes rotate {
    0% {
      -webkit-transform: rotateX(-37.5deg) rotateY(45deg);
              transform: rotateX(-37.5deg) rotateY(45deg);
    }
    50% {
      -webkit-transform: rotateX(-37.5deg) rotateY(405deg);
              transform: rotateX(-37.5deg) rotateY(405deg);
    }
    100% {
      -webkit-transform: rotateX(-37.5deg) rotateY(405deg);
              transform: rotateX(-37.5deg) rotateY(405deg);
    }
  }
  
  @keyframes rotate {
    0% {
      -webkit-transform: rotateX(-37.5deg) rotateY(45deg);
              transform: rotateX(-37.5deg) rotateY(45deg);
    }
    50% {
      -webkit-transform: rotateX(-37.5deg) rotateY(405deg);
              transform: rotateX(-37.5deg) rotateY(405deg);
    }
    100% {
      -webkit-transform: rotateX(-37.5deg) rotateY(405deg);
              transform: rotateX(-37.5deg) rotateY(405deg);
    }
  }
  
  .sides {
    -webkit-animation: rotate 3s ease infinite;
            animation: rotate 3s ease infinite;
    -webkit-animation-delay: 0.8s;
            animation-delay: 0.8s;
    -webkit-transform-style: preserve-3d;
            transform-style: preserve-3d;
    -webkit-transform: rotateX(-37.5deg) rotateY(45deg);
            transform: rotateX(-37.5deg) rotateY(45deg);
  }
  
  .cube .sides .top {
    -webkit-animation: top-animation 3s ease infinite;
            animation: top-animation 3s ease infinite;
    -webkit-animation-delay: 0ms;
            animation-delay: 0ms;
    -webkit-transform: rotateX(90deg) translateZ(96px);
            transform: rotateX(90deg) translateZ(96px);
    -webkit-animation-fill-mode: forwards;
            animation-fill-mode: forwards;
    -webkit-transform-origin: 50% 50%;
            transform-origin: 50% 50%;
  }
  
  @-webkit-keyframes top-animation {
    0% {
      opacity: 1;
      -webkit-transform: rotateX(90deg) translateZ(100px);
              transform: rotateX(90deg) translateZ(100px);
    }
    20% {
      opacity: 1;
      -webkit-transform: rotateX(90deg) translateZ(48px);
              transform: rotateX(90deg) translateZ(48px);
    }
    70% {
      opacity: 1;
